/* src/css/Footer.css */

.mb-footer {
  .mb-footer {
  border-top: 1px solid #919191;
  background-color: #3a3a3a;
  margin-top: 16px; 
  font-family: "Pretendard", "Noto Sans KR", system-ui, -apple-system,
    BlinkMacSystemFont, "Segoe UI", sans-serif;
}
}

.mb-footer-inner {
  max-width: 1120px;
  margin: 0 auto;
  padding: 24px 16px 28px;
  display: flex;
  gap: 40px;
  justify-content: space-between;
  flex-wrap: wrap;
}

/* ê³µí†µ ì»¬ëŸ¼ */
.mb-footer-col {
  flex: 1 1 240px;
  min-width: 220px;
}

/* ì™¼ìª½ : ë¡œê³  ì˜ì—­ */
.mb-footer-logo-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.mb-footer-logo {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: #111827;
}

.mb-footer-badge {
  font-size: 11px;
  padding: 2px 8px;
  border-radius: 999px;
  border: 1px solid #e5e7eb;
  background-color: #f9fafb;
  color: #6b7280;
}

.mb-footer-desc {
  font-size: 14px;
  color: #4b5563;
  margin-bottom: 10px;
}

.mb-footer-small {
  font-size: 12px;
  color: #9ca3af;
  margin: 2px 0;
}

/* ê°€ìš´ë° : ë§í¬ ì˜ì—­ */
.mb-footer-links {
  display: flex;
  gap: 32px;
  justify-content: center;
}

.mb-footer-links-group h4 {
  font-size: 13px;
  font-weight: 600;
  color: #111827;
  margin-bottom: 8px;
}

.mb-footer-links-group a {
  display: block;
  font-size: 13px;
  color: #6b7280;
  text-decoration: none;
  padding: 3px 0;
}

.mb-footer-links-group a:hover {
  color: #111827;
}

/* ì˜¤ë¥¸ìª½ : íŒ€ + ì†Œì…œ + ì¹´í”¼ë¼ì´íŠ¸ */
.mb-footer-team h4 {
  font-size: 13px;
  font-weight: 600;
  color: #111827;
  margin-bottom: 8px;
}

.mb-footer-team ul {
  list-style: none;
  padding: 0;
  margin: 0 0 10px;
}

.mb-footer-team li {
  font-size: 13px;
  color: #4b5563;
  margin-bottom: 3px;
}

.mb-footer-social {
  font-size: 13px;
  margin-bottom: 8px;
  color: #6b7280;
}

.mb-footer-social a {
  font-size: 13px;
  color: #6b7280;
  text-decoration: none;
}

.mb-footer-social a:hover {
  color: #111827;
}

.mb-footer-dot {
  margin: 0 4px;
  color: #d1d5db;
}

.mb-footer-copy {
  font-size: 12px;
  color: #9ca3af;
  margin-top: 4px;
}

/* ë°˜ì‘í˜• */
@media (max-width: 900px) {
  .mb-footer-inner {
    flex-direction: column;
    gap: 24px;
  }

  .mb-footer-links {
    justify-content: flex-start;
  }
}
