/* src/pages/Home.css */
.home {
  font-family: "Pretendard", "Noto Sans KR", system-ui, sans-serif;
  padding-bottom: 100px; /* ë©”ì¸ ë‚´ìš©ê³¼ í‘¸í„° ì‚¬ì´ ì—¬ë°± */
}

/* ===== HERO ===== */
.hero {
  max-width: 1040px;
  margin: 60px auto 12px;
  padding: 0 20px;
  text-align: center;
}
.hero-icons {
  display: flex;
  justify-content: center;
  gap: 10px;
  font-size: 28px;
  margin-bottom: 14px;
}
.hero-title {
  font-size: clamp(28px, 6vw, 56px);
  line-height: 1.05;
  font-weight: 900;
  letter-spacing: -0.5px;
  color: #111827;
  margin: 12px 0 14px;
}
.br-md {
  display: none;
  margin-bottom: 17px;
}
@media (min-width: 820px) {
  .br-md {
    display: inline;
  }
}

.accent {
  display: inline-block; /* margin-top ì ìš©ë˜ê²Œ */
  margin-top: 6px; /* ìœ„ ì¤„ê³¼ ê°„ê²© */
  padding: 0.06em 0.34em 0.1em; /* ì„¸ë¡œ íŒ¨ë”©ë„ ì‚´ì§ */
  background: #000000;
  color: #fff;
  padding: 0 0.34em;
  border-radius: 10px;
}
.hero-sub {
  color: #4b5563;
  font-size: clamp(14px, 2.2vw, 18px);
  margin: 8px auto 18px;
  max-width: 840px;
}
.hero-cta {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 8px;
}
.cta {
  text-decoration: none;
  font-weight: 800;
  padding: 12px 18px;
  border-radius: 12px;
  border: 1px solid transparent;
}
.cta.primary {
  background: #111827;
  color: #fff;
}
.cta.primary:hover {
  opacity: 0.92;
}
.cta.secondary {
  background: #fff;
  color: #111827;
  border-color: #e5e7eb;
}
.cta.secondary:hover {
  background: #f9fafb;
}

.hero-badges {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 14px;
}
.hero-badges span {
  font-size: 12px;
  background: #f3f4f6;
  color: #374151;
  padding: 6px 10px;
  border-radius: 999px;
}



/* ===== FEATURES ===== */
.features {
  max-width: 1040px;
  margin: 40px auto 40px;
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 12px;
}
@media (max-width: 860px) {
  .features {
    grid-template-columns: 1fr;
  }
}
.feat {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  padding: 16px;
  text-align: left;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.04);
}
.feat-emoji {
  font-size: 22px;
  margin-bottom: 6px;
}
.feat-title {
  font-weight: 800;
  margin-bottom: 6px;
}
.feat-desc {
  color: #4b5563;
}

/* ë§í¬ê°€ ì¹´ë“œ ì „ì²´ë¥¼ ê°ì‹¸ë¯€ë¡œ ê¸°ë³¸ ë§í¬ ìŠ¤íƒ€ì¼ ì œê±° */
.feat-link {
  display: block;
  text-decoration: none;
  color: inherit;
  outline: none;
}

/* ì¹´ë“œ ìƒí˜¸ì‘ìš© */
.feat {
  transition: transform 0.12s ease, box-shadow 0.12s ease,
    border-color 0.12s ease;
  cursor: pointer;
}
.feat:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 26px rgba(0, 0, 0, 0.06);
  border-color: #dfe3e8;
}
/* í‚¤ë³´ë“œ ì ‘ê·¼ì„±: íƒ­ í¬ì»¤ìŠ¤ ë§ */
.feat-link:focus-visible .feat {
  box-shadow: 0 0 0 4px rgba(17, 24, 39, 0.12);
  transform: translateY(-1px);
}

/* ë‚´ë¶€ í…ìŠ¤íŠ¸/ì•„ì´ì½˜ë§Œ í´ë¦­í•´ë„ ì¹´ë“œë¡œ ì´ë²¤íŠ¸ê°€ ê°€ë„ë¡ (ì„ íƒì‚¬í•­) */
.feat * {
  pointer-events: none;
}

/* ===== DEV / TEST ì˜ì—­ ===== */
.dev {
  max-width: 1040px;
  margin: 20px auto 80px;
  padding: 0 20px 24px;
  border-top: 1px dashed #e5e7eb;
}
.dev h2 {
  margin-top: 20px;
}
.dev-buttons {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin: 12px 0 14px;
}
.google-login-btn,
.save-emails-btn {
  border-radius: 10px;
  padding: 10px 14px;
  font-weight: 700;
  border: 1px solid #e5e7eb;
  background: #fff;
  cursor: pointer;
}
.google-login-btn:hover,
.save-emails-btn:hover {
  background: #f9fafb;
}

.message {
  padding: 10px 12px;
  border-radius: 10px;
  margin-bottom: 10px;
  font-weight: 600;
}
.message.success {
  background: #ecfdf5;
  color: #065f46;
}
.message.error {
  background: #fef2f2;
  color: #991b1b;
}

.email-list-container {
  margin-top: 12px;
}
.no-emails {
  color: #6b7280;
}

.email-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;
}
