.write-form {
  padding: 16px 20px;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

/* form ë‚´ë¶€ */
.write-form form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* ê³µí†µ ì…ë ¥ */
.write-field input,
.write-field textarea {
  width: 100%;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 10px 12px;
  font-size: 14px;
  background: #ffffff;
  box-sizing: border-box;
}

/* í¬ì»¤ìŠ¤ (ë³´ë¼ ì œê±°) */
.write-field input:focus,
.write-field textarea:focus {
  border-color: #d1d5db;
  box-shadow: 0 0 0 3px rgba(209, 213, 219, 0.4);
  outline: none;
}

.write-label {
  font-size: 13px;
  color: #64748b;
  margin-bottom: 4px;
  font-weight: 600;
  display: block;
}

.write-field-row > div {
  flex: 1;
}

/* ë‚ ì§œ input ë°•ìŠ¤ */
.write-datebox {
  background: #fafafa;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 6px 10px;
  display: flex;
  align-items: center;
}

.write-datebox input {
  border: none !important;
  background: transparent;
  padding: 0;
  font-size: 14px;
  width: 100%;
}

.write-datebox input:focus {
  box-shadow: none !important;
  outline: none !important;
}

/* ë²„íŠ¼ ë‹´ëŠ” ë°•ìŠ¤ */
.write-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-bottom: 6px;
}

/* ê³µí†µ ë²„íŠ¼ */
.write-actions button {
  border-radius: 8px;
  padding: 8px 14px;
  cursor: pointer;
  font-weight: 600;
  font-size: 13px;
  border: 1px solid #e5e7eb;
  background: #f9fafb;
  color: #111;
}

/* ì €ì¥/ìˆ˜ì • */
.write-btn-primary {
  background: #4f46e5;
  border-color: #4f46e5;
  color: white;
}

.write-btn-primary:hover {
  background: #4338ca;
}

