/* src/pages/Contact.css */
:root {
  --text: #111827;
  --muted: #6b7280;
  --border: #e5e7eb;
  --bg: #ffffff;
  --bg-soft: #f8fafc;
  --accent: #111827;
}

.chat-wrap {
  max-width: 950px;
  margin: 0 auto;
  padding: 16px 12px;
  font-family: "Pretendard", "Noto Sans KR", system-ui, sans-serif;
}

/* ì¹´ë“œ ì „ì²´ë¥¼ ì„¸ë¡œ ë ˆì´ì•„ì›ƒìœ¼ë¡œ */
.chat-card {
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);
  padding: 14px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  min-height: 480px;
}

/* Header */
.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 10px 12px;
}
.chat-title {
  font-weight: 900;
  color: var(--text);
  letter-spacing: -0.2px;
}
.chat-me {
  color: var(--muted);
  font-weight: 600;
}

/* Message list */
.chat-list {
  flex: 1; /* ë‚¨ì€ ì˜ì—­ ì±„ìš°ê¸° */
  min-height: 320px;
  max-height: 520px;
  overflow-y: auto;
  padding: 10px;
  background: var(--bg-soft);
  border-radius: 12px;
  border: 1px solid var(--border);
}

/* Scrollbar (optional) */
.chat-list::-webkit-scrollbar {
  width: 8px;
}
.chat-list::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 999px;
}
.chat-list::-webkit-scrollbar-track {
  background: transparent;
}

/* Message item */
.chat-item {
  display: flex;
  gap: 8px;
  margin: 8px 4px;
  align-items: flex-end;
}
.chat-item.isMe {
  justify-content: flex-end;
}
.chat-item.event {
  justify-content: center;
}

/* Bubble */
.bubble {
  max-width: 72%;
  background: #fff;
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 8px 11px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.04);
  color: var(--text);
  font-size: 0.9rem;
  line-height: 1.5;
  position: relative;
}

/* ë‚´ ë©”ì‹œì§€ ë§í’ì„  */
.chat-item.isMe .bubble {
  background: var(--accent);
  color: #fff;
  border-color: #020617;
}

/* Bubble inner */
.sender {
  font-size: 11px;
  color: var(--muted);
  margin-bottom: 3px;
  font-weight: 600;
}
.chat-item.isMe .sender {
  color: #e5e7eb;
}
.text {
  white-space: pre-wrap;
  word-break: break-word;
}

/* ì‹œê°„/ì½ìŒ ë©”íƒ€ ì˜ì—­ */
.meta-row {
  display: flex;
  align-items: center;
  gap: 6px;
  justify-content: flex-end;
  margin-top: 4px;
  font-size: 11px;
  color: #6b7280;
}
.time {
  font-size: 11px;
  color: var(--muted);
}
.chat-item.isMe .time {
  color: #d1d5db;
}

/* ìë™ì‘ë‹µ ë²„íŠ¼ */
.bubble-btns {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}
.bubble-btn {
  min-width: 96px;
  border: 1px solid #d1d5db;
  border-radius: 999px;
  padding: 6px 10px;
  font-weight: 600;
  cursor: pointer;
  background: #ffffff;
  color: #111827;
  font-size: 0.85rem;
  transition: background 0.15s ease, color 0.15s ease, border-color 0.15s ease,
    box-shadow 0.15s ease;
}
.bubble-btn:hover {
  background-color: #111827;
  color: #ffffff;
  border-color: #020617;
  box-shadow: 0 2px 6px rgba(15, 23, 42, 0.3);
}

/* System/event message */
.event-text {
  font-size: 13px;
  color: #1f2937;
  background: #f3f4f6;
  border: 1px solid var(--border);
  padding: 4px 8px;
  border-radius: 10px;
  font-weight: 700;
}

/* Input row */
.chat-input-row {
  display: flex;
  gap: 8px;
  margin-top: 4px;
}
.chat-input {
  flex: 1;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 10px 12px;
  resize: none;
  outline: none;
  background: #fff;
  color: var(--text);
  transition: box-shadow 0.15s, border-color 0.15s, background 0.15s;
  font-size: 0.9rem;
}
.chat-input::placeholder {
  color: #9ca3af;
}
.chat-input:focus {
  border-color: #c7c9ce;
  box-shadow: 0 0 0 4px rgba(17, 24, 39, 0.08);
}

/* Send button */
.chat-send {
  min-width: 96px;
  border: none;
  border-radius: 12px;
  padding: 10px 14px;
  font-weight: 800;
  cursor: pointer;
  background: #000000;
  color: #fff;
  transition: opacity 0.15s ease, transform 0.02s ease;
  font-size: 0.9rem;
}
.chat-send:hover {
  opacity: 0.94;
}
.chat-send:active {
  transform: translateY(1px);
}
.chat-send:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Responsive */
@media (max-width: 560px) {
  .chat-card {
    padding: 10px;
  }
  .bubble {
    max-width: 86%;
    font-size: 0.85rem;
  }
  .chat-list {
    min-height: 260px;
    max-height: 420px;
  }
}
