.chatlist-wrap {
  padding: 20px;
}
.chatlist-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.chatlist-item {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
}
.chatlist-item:hover {
  background: #f3f3f3;
}
.chatlist-user {
  font-weight: bold;
}
.chatlist-msg {
  color: #555;
  margin-top: 4px;
}
.chatlist-date {
  font-size: 12px;
  color: #999;
  text-align: right;
}

.chatlist-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
}
.chatlist-item:hover {
  background: #f9f9f9;
}
.chatlist-user {
  font-weight: 600;
  color: #333;
  min-width: 120px;
}
.chatlist-msg {
  flex: 1;
  margin: 0 12px;
  color: #666;
  font-size: 0.9rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.chatlist-date {
  font-size: 0.8rem;
  color: #999;
  white-space: nowrap;
}
.chat-delete {
  margin-left: 12px;
  padding: 6px 10px;
  background: #f87171;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
.chat-delete:hover {
  color: #dc2626;
}
