/* ===== AddressBook ê¸°ë³¸ ë ˆì´ì•„ì›ƒ ===== */
.ab-wrap {
  max-width: 980px;
  min-height: 600px;
  margin: 0 auto;
  padding: 16px 12px;
  font-family: "Pretendard", "Noto Sans KR", system-ui, -apple-system, Segoe UI,
    Roboto, sans-serif;
  color: #1f2937; /* slate-800 */
}

/* ì œëª© + ìš°ì¸¡ ë²„íŠ¼ ë¼ì¸ */
.ab-title-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 14px;
}

.ab-title {
  font-size: 1.35rem;
  font-weight: 800;
  letter-spacing: 0.2px;
  color: #111827; /* slate-900 */
}

/* ìƒë‹¨ ì—ëŸ¬ ë°°ì§€ */
.ab-error {
  background: #fee2e2; /* red-100 */
  color: #b91c1c; /* red-700 */
  border: 1px solid #fecaca;
  padding: 10px 12px;
  border-radius: 12px;
  margin-bottom: 10px;
}

/* ===== ì¹´ë“œ ì»¨í…Œì´ë„ˆ ===== */
.ab-card {
  background: #ffffff;
  border: 1px solid #e5e7eb; /* gray-200 */
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  /* ëª¨ë°”ì¼ì—ì„œ ì»¬ëŸ¼ì´ ë„˜ì¹˜ë©´ ê°€ë¡œ ìŠ¤í¬ë¡¤ í—ˆìš© */
  overflow-x: auto;
}

/* í—¤ë” ì˜ì—­ */
.ab-head {
  display: grid;
  grid-template-columns: 1.2fr 2fr 0.8fr;
  align-items: center;
  gap: 8px;
  padding: 12px 14px;
  background: #f8fafc; /* slate-50 */
  border-bottom: 1px solid #e5e7eb;
  font-weight: 700;
  color: #0f172a; /* slate-900 */
}

/* ë³¸ë¬¸ ë˜í¼ */
.ab-body {
  display: flex;
  flex-direction: column;
}

/* ë°ì´í„° ë¡œìš° */
.ab-row {
  display: grid;
  grid-template-columns: 1.2fr 2fr 0.8fr;
  align-items: center;
  gap: 8px;
  padding: 12px 14px;
  border-top: 1px solid #f1f5f9; /* slate-100 */
  transition: background-color 0.15s ease, transform 0.15s ease;
}

.ab-row:hover {
  background: #f9fafb; /* gray-50 */
}

/* ì»¬ëŸ¼ ê³µí†µ */
.ab-col {
  min-width: 0; /* í…ìŠ¤íŠ¸ ellipsisë¥¼ ìœ„í•´ */
}

/* ì´ë¦„/ì´ë©”ì¼/ì•¡ì…˜ ì„¸ë¶€ */
.ab-name {
  font-weight: 600;
  color: #111827;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ab-email {
  color: #374151; /* gray-700 */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ab-action {
  display: flex;
  justify-content: flex-end;
}

/* ë¹ˆ ìƒíƒœ */
.ab-empty {
  padding: 24px 16px;
  color: #64748b; /* slate-500 */
  text-align: center;
}

