/* ìš°ì¸¡ ìƒë‹¨ ì£¼ìš” ë²„íŠ¼ (Gmail ì—´ê¸°) â€” ë¯¼íŠ¸í†¤ */
.ab-primary-btn {
  appearance: none;
  border: 1px solid #000000; /* ë°ì€ ë¯¼íŠ¸ */
  background: #ffffff; /* ë¯¼íŠ¸ íŒŒìŠ¤í…” */
  color: #124f37; /* ì§„í•œ ì´ˆë¡í‹° */
  padding: 10px 14px;
  border-radius: 12px;
  font-weight: 700;
  cursor: pointer;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.06);
  transition: transform 0.12s ease, box-shadow 0.12s ease,
    background-color 0.12s ease, border-color 0.12s ease;
}

.ab-primary-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
  background: #ffffff; /* ë„¤ë¹„ê²Œì´ì…˜ë°”ì— ë§ì¶˜ í†¤ */
  border-color: #030303;
}

.ab-primary-btn:active {
  transform: translateY(0);
}

.ab-primary-btn:focus-visible {
  outline: 3px solid #929292; /* mint focus ring */
  outline-offset: 2px;
}

/* ê° í–‰ì˜ ë©”ì¼ì“°ê¸° ë²„íŠ¼ â€” ì•„ì›ƒë¼ì¸ ë¯¼íŠ¸ */
.ab-mail-btn {
  appearance: none;
  border: 1.5px solid #a5d6b5;
  background: #ffffff;
  color: #0f5132; /* ì§™ì€ ê·¸ë¦° */
  padding: 8px 12px;
  border-radius: 10px;
