}

.ab-search-input {
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 8px 15px;
  font-size: 15px;
  outline: none;
  background-color: #ffffff;
}

/*í˜ì´ì§• */
/* í˜ì´ì§• ì˜ì—­ ì „ì²´ */
.ab-pagination {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 6px;
}

/* í˜ì´ì§• ë²„íŠ¼ ê¸°ë³¸ ìŠ¤íƒ€ì¼ */
.ab-page-btn {
  min-width: 32px;
  padding: 6px 10px;
  border-radius: 500px;
  border: 1px solid #e5e7eb;
  background-color: #ffffff;
