  border-color: #d1d5db;
}

/* í˜„ì¬ ì„ íƒëœ í˜ì´ì§€ */
.ab-page-btn.active {
  background-color: #111827;
  color: #ffffff;
  border-color: #111827;
  transform: translateY(-1px);
}

.ab-search-input:focus {
