/* src/layout/Navbar.css */
.navbar {
  position: sticky;
  top: 0;
  z-index: 50;
  background: #fff;
  border-bottom: 1px solid #e5e7eb;
  backdrop-filter: saturate(180%) blur(6px);
  display: grid;
  grid-template-columns: 1fr auto 1fr; /* ì¢Œ/ì¤‘ì•™/ìš° */
  align-items: center;
  padding: 14px 28px;
  font-family: "Pretendard", "Noto Sans KR", system-ui, sans-serif;
}

/* ì¢Œì¸¡ */
.brand {
  font-weight: 800;
  font-size: 18px;
  letter-spacing: 0.2px;
  color: #111827;
  text-decoration: none;
}
.brand-emoji {
  margin-left: 4px;
}

/* ì¤‘ì•™ ë©”ë‰´ */
.nav-center {
  justify-self: center;
  display: flex;
  gap: 22px;
}
.nav-center a {
  color: #111827;
  text-decoration: none;
  font-weight: 600;
  padding: 8px 10px;
  border-radius: 10px;
}
.nav-center a:hover {
  background: #f3f4f6;
}

/* ìš°ì¸¡ ë²„íŠ¼ */
.nav-right {
  justify-self: end;
  display: flex;
  gap: 10px;
}
.btn {
  border-radius: 10px;
  padding: 8px 12px;
  font-weight: 700;
  border: 1px solid transparent;
  cursor: pointer;
  text-decoration: none;
}
.btn.ghost {
  border-color: #e5e7eb;
  background: #fff;
  color: #111827;
}
.btn.ghost:hover {
  background: #f9fafb;
}
.btn.solid {
  background: #111827;
  color: #fff;
}
.btn.solid:hover {
  background: #979797;
}

.google-login-btn {
  border-radius: 10px;
  padding: 10px 14px;
  font-weight: 700;
  border: 2px solid #0a0a0a;
  background: #1b1b1b;
  cursor: pointer;
}

