.error-btn {
  border: none;
  padding: 8px 14px;
  background: #fee2e2;
  color: #491111;
  font-size: 14px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.error-btn:hover {
  background: #fecaca;
}

.login-btn {
  border: none;
  padding: 8px 14px;
  background: #e7e7e7;
  color: #303030;
  font-size: 14px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.login-btn:hover {
  background: #c5c5c5;
}
