.mb-brief-bar {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 14px;
  margin-bottom: 10px;
  border-radius: 999px;
  border: 1px solid #e5e7eb;
  background-color: #f9fafb;
  font-size: 0.9rem;
  color: #111827;
  width: 100%;
  box-sizing: border-box;
}

.mb-brief-icon {
  font-size: 1.1rem;
  flex-shrink: 0;
}

.mb-brief-text-wrap {
  display: flex;
  flex-direction: column;
  gap: 2px;
  min-width: 0;
}

.mb-brief-main {
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mb-brief-sub {
  font-size: 0.85rem;
  color: #6b7280;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mb-brief-loading {
  margin-left: auto;
  font-size: 0.75rem;
  color: #9ca3af;
}

