@media (max-width: 768px) {
  .mb-brief-bar {
    padding: 8px 12px;
    gap: 8px;
    font-size: 0.85rem;
  }

  .mb-brief-icon {
    font-size: 1rem;
  }

  .mb-brief-main {
    font-size: 0.9rem;
  }

  .mb-brief-sub {
    font-size: 0.8rem;
  }
}

