.mb-terms-page {
  max-width: min(900px, 96vw);
  margin: 80px auto 40px auto;
  padding: 0 12px;
}

.mb-terms-inner {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  padding: 24px 28px;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.06);
}

.mb-terms-header h1 {
  font-size: 1.6rem;
  font-weight: 800;
  margin-bottom: 6px;
  color: #0f172a;
}

.mb-terms-header p {
  margin: 2px 0;
  color: #4b5563;
  font-size: 0.95rem;
}

.mb-terms-updated {
  margin-top: 6px;
  font-size: 0.85rem;
  color: #6b7280;
}

.mb-terms-section {
  margin-top: 20px;
}

.mb-terms-section h2 {
  font-size: 1.05rem;
  font-weight: 700;
  margin-bottom: 8px;
  color: #111827;
}

.mb-terms-section p,
.mb-terms-section li {
  font-size: 0.94rem;
  line-height: 1.6;
  color: #374151;
}

.mb-terms-section ol {
  padding-left: 1.2rem;
}
