/* ============================
   DETAIL VIEW
   ============================ */

.detail-form {
  padding: 18px 22px;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

/* ============================
   ìƒë‹¨ ë‚ ì”¨
   ============================ */

.detail-top {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 18px;
}

.detail-weather {
  font-weight: 700;
  color: #111827;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  flex-wrap: wrap;
}

/* ============================
   ë³¸ë¬¸
   ============================ */

.detail-view {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.detail-label {
  font-size: 13px;
  color: #64748b;
  margin-bottom: 4px;
  font-weight: 600;
}

.detail-field {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.detail-box,
.detail-notes,
.detail-time {
  background: #fafafa;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 10px 14px;
  font-size: 14px;
  color: #111827;
}

.detail-datechip {
  background: #fafafa;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 10px 12px;
  font-size: 14px;
  color: #111827;
}

/* ============================
   ì§€ë„ í† ê¸€ + ì§€ë„
