.detail-map-toggle {
  width: 100%;
  background: hsl(0, 0%, 95%);
  padding: 8px 12px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  border: 1px solid #d1d5db;
  text-align: center;
}

.detail-map-wrapper {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.35s ease;
  width: 100%;

  display: flex; /* ì¶”ê°€ */
  justify-content: center; /* ì¶”ê°€ */
}

.detail-map-wrapper.open {
  max-height: 350px;
}

