:root {
  --text: #111827;
  --muted: #6b7280;
  --border: #e5e7eb;
  --bg: #ffffff;
  --accent: #111827; /* ë²„íŠ¼/í¬ì»¤ìŠ¤ */
  --error-text: #b91c1c;
}

.login-container {
  min-height: calc(100dvh - 64px); /* ìƒë‹¨ ë„¤ë¹„ ë†’ì´ ê³ ë ¤ ì—¬ìœ  */
  display: grid;
  align-content: start; /* ì„¸ë¡œ: ìœ„ìª½ìœ¼ë¡œ ë¶™ì„ */
  justify-content: center; /* ê°€ë¡œ: ê°€ìš´ë° ìœ ì§€ */
  padding: 30px 16px;
  background: #fafafa;
  margin-top: 50px;
}

.login-box {
  width: 100%;
  max-width: 420px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.06);
  padding: 28px;
}

.login-box h2 {
  margin: 0 0 8px;
  font-size: 26px;
  font-weight: 900;
  color: var(--text);
  letter-spacing: -0.2px;
}

.login-box p.helper {
  margin: 0 0 16px;
  color: var(--muted);
  font-size: 14px;
}

/* ì—ëŸ¬ ë©”ì‹œì§€: í…ìŠ¤íŠ¸ë§Œ */
.error-message {
  color: #b91c1c; /* ì„ ëª…í•œ ë ˆë“œ (#dc2626~#b91c1c ê³„ì—´ ì¶”ì²œ) */
  font-weight: 700; /* êµµê²Œ */
  margin: 8px 0 12px; /* ìœ„ì•„ë˜ ì—¬ë°±ë§Œ */
  padding: 0; /* ë°°ê²½ ì—†ìœ¼ë‹ˆ íŒ¨ë”© ì œê±° */
  border: 0; /* í…Œë‘ë¦¬ ì œê±° */
}

.login-box .error-message {
  background: transparent !important;
  border: 1px solid #f7cbcb;
  border-radius: 10px;
  box-shadow: none !important;
  padding: 5px;
  color: #f04545;
  font-size: 15px;
  font-weight: 600;
  margin: 8px 0 12px;
  text-align: center;
}

/* í¼ */
.login-box form {
  display: grid;
  gap: 10px;
  margin-top: 6px;
}

.login-box input[type="text"],
.login-box input[type="password"] {
  width: 100%;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid var(--border);
  background: #fff;
  color: var(--text);
  font-size: 15px;
  outline: none;
  transition: box-shadow 0.15s ease, border-color 0.15s ease,
    background 0.15s ease;
}

.login-box input::placeholder {
  color: #9ca3af;
}

.login-box input:focus {
  border-color: #c7c9ce;
  box-shadow: 0 0 0 4px rgba(17, 24, 39, 0.08);
}

/* ë²„íŠ¼ */
.login-box button[type="submit"] {
  margin-top: 6px;
  width: 100%;
  border: none;
  border-radius: 12px;
  padding: 12px 16px;
  font-weight: 800;
  font-size: 15px;
  background: var(--accent);
  color: #fff;
  cursor: pointer;
  transition: opacity 0.15s ease, transform 0.02s ease;
}
.login-box button[type="submit"]:hover {
  opacity: 0.94;
}
.login-box button[type="submit"]:active {
  transform: translateY(1px);
}
.login-box button[type="submit"]:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* í•˜ë‹¨ ë§í¬ */
.sub-actions {
  margin-top: 14px;
  text-align: center;
  color: var(--muted);
  font-size: 14px;
}
.sub-actions span {
  color: var(--text);
  font-weight: 800;
  cursor: pointer;
  padding: 4px 6px;
  border-radius: 8px;
}
.sub-actions span:hover {
  background: #f3f4f6;
}

/* ì‘ì€ í™”ë©´ ë³´ì • */
@media (max-width: 420px) {
  .login-box {
    padding: 22px;
  }
  .login-box h2 {
    font-size: 24px;
  }
}
