/* src/pages/MyPage.css */

/* 1) ì „ì—­ ë°•ìŠ¤ì‚¬ì´ì§• ë¦¬ì…‹ */
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --text: #111827;
  --muted: #6b7280;
  --border: #e5e7eb;
  --bg: #ffffff;
  --card-shadow: 0 12px 30px rgba(0, 0, 0, 0.06);
  --accent: #111827; /* ë²„íŠ¼/í¬ì»¤ìŠ¤ */
}

.mypage-container {
  max-width: 650px; /* ìš”ì²­ê°’ ìœ ì§€ */
  margin: 0 auto;
  padding: 16px 12px 32px;
  font-family: "Pretendard", "Noto Sans KR", system-ui, sans-serif;
  color: var(--text);
}

.mypage-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 14px;
}

.mypage-header h2 {
  margin: 0;
  font-size: 1.35rem;
  font-weight: 900;
  letter-spacing: -0.2px;
  color: var(--text);
}

/* ì¹´ë“œ */
.mypage-card {
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 16px;
  box-shadow: var(--card-shadow);
  padding: 20px;
  overflow: hidden; /* ë¼ìš´ë“œ ë°–ìœ¼ë¡œ ë‚´ìš© ë¹„ì¹˜ì§€ ì•Šê²Œ */
}

/* ë¼ë²¨-ì¸í’‹ ê·¸ë¦¬ë“œ */
.mypage-grid {
  display: grid;
  grid-template-columns: 140px 1fr; /* ë¼ë²¨ / ì¸í’‹ */
  gap: 12px 14px;
  align-items: center;
}

/* 2) Grid ì•„ì´í…œì´ ì…€ ë°”ê¹¥ìœ¼ë¡œ ëŠ˜ì–´ë‚˜ì§€ ì•Šê²Œ */
.mypage-grid > * {
  min-width: 0;
}

/* ë¼ë²¨ */
.mypage-grid .label {
  font-weight: 800;
  color: var(--text);
  white-space: nowrap;
}

/* ì¸í’‹ ê³µí†µ */
.mypage-grid input[type="text"],
.mypage-grid input[type="password"] {
  width: 100%;
  max-width: 100%;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid var(--border);
  background: #fff;
  color: var(--text);
  font-size: 15px;
  outline: none;
  transition: box-shadow 0.15s ease, border-color 0.15s ease,
    background 0.15s ease;
  box-sizing: border-box; /* 3) í•µì‹¬: íŒ¨ë”©/ë³´ë” í¬í•¨í•˜ì—¬ 100% ì•ˆì—ì„œ ê³„ì‚° */
}

.mypage-grid input::placeholder {
  color: #9ca3af;
}

.mypage-grid input:focus {
  border-color: #c7c9ce;
  box-shadow: 0 0 0 4px rgba(17, 24, 39, 0.08);
}

/* ì½ê¸° ì „ìš©(ì•„ì´ë”” ë“±) */
.ro-input {
  background: #f9fafb !important;
  color: #4b5563 !important;
  border-color: #eef0f3 !important;
  cursor: not-allowed;
}

/* ì—ëŸ¬ ë©”ì‹œì§€ */
.error-message {
  background: transparent !important;
  border: 1px solid #f7cbcb;
  border-radius: 10px;
  box-shadow: none !important;
  padding: 5px;
  color: #f04545;
  font-size: 15px;
  font-weight: 600;
  margin: 8px 0 12px;
  text-align: center;
}

/* ì œì¶œ ë²„íŠ¼ */
.submit-btn {
  margin-top: 14px;
  width: 100%;
  border: none;
  border-radius: 12px;
  padding: 12px 16px;
  font-weight: 800;
  font-size: 15px;
  background: --accent;
  background: var(--accent);
  color: #fff;
  cursor: pointer;
  transition: opacity 0.15s ease, transform 0.02s ease;
}

.submit-btn:hover {
  opacity: 0.94;
}
.submit-btn:active {
  transform: translateY(1px);
}
.submit-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ë¡œë”© í…ìŠ¤íŠ¸ */
.loading {
  padding: 12px;
  color: #6b7280;
}

/* ë°˜ì‘í˜• */
@media (max-width: 720px) {
  .mypage-grid {
    grid-template-columns: 1fr;
  }
  .mypage-grid .label {
    margin-top: 6px;
  }
  .mypage-card {
    padding: 16px;
  }
}

@media (max-width: 420px) {
  .mypage-container {
    padding: 12px 10px 24px;
  }
  .mypage-header h2 {
    font-size: 1.2rem;
  }
}
