/* src/pages/Signup.css */

/* ========== í† í° ========== */
:root {
  --text: #111827;
  --muted: #6b7280;
  --border: #e5e7eb;
  --bg: #ffffff;
  --accent: #111827; /* ë²„íŠ¼/í¬ì»¤ìŠ¤ */
  --error-text: #b91c1c;
}

/* ========== ë ˆì´ì•„ì›ƒ ========== */
.signup-container {
  min-height: calc(100dvh - 64px);
  display: grid;
  align-content: start; /* ì„¸ë¡œ: ìœ„ìª½ìœ¼ë¡œ ë¶™ì„ */
  justify-content: center; /* ê°€ë¡œ: ê°€ìš´ë° ìœ ì§€ */
  padding: 30px 16px;
  background: #fafafa;
  margin-top: 50px;
}

.signup-box {
  width: 100%;
  max-width: 420px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.06);
  padding: 28px;
}

.signup-box h2 {
  margin: 0 0 8px;
  font-size: 26px;
  font-weight: 900;
  color: var(--text);
  letter-spacing: -0.2px;
}
.signup-box .helper {
  margin: 0 0 16px;
  color: var(--muted);
  font-size: 14px;
}

/* ========== ì—ëŸ¬ (í…ìŠ¤íŠ¸ë§Œ) ========== */
.error-message {
  color: var(--error-text);
  font-weight: 700;
  margin: 8px 0 12px;
  padding: 0;
  border: 0;
  background: transparent;
}
.field-error {
  color: var(--error-text);
  font-size: 13px;
  font-weight: 700;
  margin-top: -2px;
}

/* ========== í¼: Flex column (ìˆœì„œ ê³ ì •) ========== */
.signup-form {
  display: flex !important; /* â† Grid ëŒ€ì‹  Flex ì‚¬ìš© */
  flex-direction: column !important; /* ìœ„â†’ì•„ë˜ */
  gap: 12px;
}

/* ê° í–‰ì€ ì„¸ë¡œë¡œ ìŒ“ì´ê²Œ */
.form-row {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

/* ì¸í’‹ */
.signup-box input[type="text"],
.signup-box input[type="password"] {
  width: 100%;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid var(--border);
  background: #fff;
  color: var(--text);
  font-size: 15px;
  outline: none;
  transition: box-shadow 0.15s ease, border-color 0.15s ease,
    background 0.15s ease;
}
.signup-box input::placeholder {
  color: #9ca3af;
}
.signup-box input:focus {
  border-color: #c7c9ce;
  box-shadow: 0 0 0 4px rgba(17, 24, 39, 0.08);
}

/* ========== ë²„íŠ¼ ì˜ì—­: í•­ìƒ ë§¨ ì•„ë˜ ========== */
.actions {
  display: flex; /* ê°€ë¡œë¡œ ë‘ ë²„íŠ¼ */
  gap: 8px;
  margin-top: 8px;
}
.actions .btn {
  flex: 1;
}

/* ë²„íŠ¼ ê³µí†µ */
.btn {
  border-radius: 12px;
  padding: 12px 16px;
  font-weight: 800;
  font-size: 15px;
  border: 1px solid transparent;
  cursor: pointer;
  text-decoration: none;
}
.btn.solid {
  background: var(--accent);
  color: #fff;
}
.btn.solid:hover {
  opacity: 0.94;
}
.btn.solid:active {
  transform: translateY(1px);
}
.btn.ghost {
  background: #fff;
  color: var(--text);
  border-color: var(--border);
}
.btn.ghost:hover {
  background: #f9fafb;
}

/* ========== ë°˜ì‘í˜• ========== */
@media (max-width: 420px) {
  .signup-box {
    padding: 22px;
  }
  .signup-box h2 {
    font-size: 24px;
  }
}
